@import url(https://fonts.googleapis.com/css?family=Inconsolata|Major+Mono+Display);
body {
  background-color: #fff;
  padding: 20px;
  margin: 0; }

h1, h2, p, ul, li {
  font-family: "Major Mono Display", sans-serif; }

ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0; }

ul.header {
  background-color: #111;
  padding: 0; }

ul.header li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block; }

.content {
  background-color: #fff;
  padding: 20px; }

.content h2 {
  padding: 0;
  margin: 0; }

.content li {
  margin-bottom: 10px; }

.active {
  background-color: #0099ff; }

.social-icon {
  height: 21px;
  width: 21px; }

.d-inline {
  list-style-type: none;
  padding: 0px;
  margin: 0px; }

